.note {
  background-color: #58a;
  position: relative;
  background: -webkit-linear-gradient(240deg,transparent 1.5em, #58a 0);
  background: -o-linear-gradient(240deg,transparent 1.5em, #58a 0);
  background: linear-gradient(-150deg,transparent 1.5em, #58a 0);
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman';
  font-size: 1em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 250px;
  height: 200px;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  color: #fff;
  border-radius: 0.5em;
  margin: 1em auto;
  -ms-flex: 0 0 22%;
      flex: 0 0 22%;
}

.note::before {
  content: '';
  position: absolute;
  top: -8px;
  right: 0;
  width: 1.73em;
  height: 3em;
  background: -webkit-gradient(linear, right top, left bottom, color-stop(50%, transparent),color-stop(0, rgba(0,0,0,0.2)), to(rgba(0,0,0,0.4)))
  100% 0 no-repeat;
  background: -webkit-linear-gradient(right top, transparent 50%,rgba(0,0,0,0.2) 0, rgba(0,0,0,0.4))
  100% 0 no-repeat;
  background: -o-linear-gradient(right top, transparent 50%,rgba(0,0,0,0.2) 0, rgba(0,0,0,0.4))
  100% 0 no-repeat;
  background: linear-gradient(to left bottom, transparent 50%,rgba(0,0,0,0.2) 0, rgba(0,0,0,0.4))
  100% 0 no-repeat;
  -webkit-transform: translateY(-1.3em) rotate(-30deg);
      -ms-transform: translateY(-1.3em) rotate(-30deg);
          transform: translateY(-1.3em) rotate(-30deg);
  -webkit-transform-origin: bottom right;
      -ms-transform-origin: bottom right;
          transform-origin: bottom right;
  border-bottom-left-radius: inherit;
  margin-top: 10px;
}

.note-btn-group {
  display: -ms-flexbox;
  display: flex;
}

.board {
  -ms-flex-direction: row;
      flex-direction: row;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

@media screen and (max-width: 500px) {
  .board {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
}


.add-btn {
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  -webkit-transition-duration: 0.4s; /* Safari */
  -o-transition-duration: 0.4s;
     transition-duration: 0.4s;
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
}

.add-btn-block {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0 auto;
}

.add-btn:hover {
  background-color: #008CBA;
  color: white;
}



